'use client'

import React, { useEffect, useState } from 'react'
import Link from 'next/link'
import { Rental } from '@/types/rental'
import { calculateRealInflationAdjustment, getNextUpdateDate, isUpdateDue, formatCurrency } from '@/lib/inflation-real'
import { getRentalUpdateStatus } from '@/lib/rental-updater'

interface RentalCardProps {
  rental: Rental
}

const RentalCard: React.FC<RentalCardProps> = ({ rental }) => {
  const [calculation, setCalculation] = useState<any>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const loadCalculation = async () => {
      try {
        const calc = await calculateRealInflationAdjustment(
          rental.baseAmount,
          rental.startDate,
          rental.updateFrequency,
          rental.currentAmount,
          rental.lastUpdateDate
        )
        setCalculation(calc)
      } catch (error) {
        console.error('Error calculando inflación:', error)
      } finally {
        setLoading(false)
      }
    }

    loadCalculation()
  }, [rental])

  const nextUpdate = getNextUpdateDate(rental.startDate, rental.updateFrequency, rental.lastUpdateDate)
  const isDue = isUpdateDue(rental.startDate, rental.updateFrequency, rental.lastUpdateDate)
  
  const formatDate = (date: Date) => {
    return date.toLocaleDateString('es-AR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    })
  }

  const currentDisplayAmount = calculation?.updatedAmount || rental.currentAmount

  return (
    <Link href={`/rental/${rental.id}`}>
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 hover:shadow-md transition-all duration-200 cursor-pointer hover:border-blue-300">
        {/* Header */}
        <div className="flex justify-between items-start mb-4">
          <h3 className="text-lg font-semibold text-gray-800 truncate pr-2">
            {rental.name}
          </h3>
          {isDue && (
            <span className="bg-red-100 text-red-800 text-xs font-medium px-2 py-1 rounded-full whitespace-nowrap">
              Vencido
            </span>
          )}
        </div>

        {/* Amounts */}
        <div className="space-y-3 mb-4">
          <div>
            <p className="text-sm text-gray-600">Monto Base</p>
            <p className="text-xl font-bold text-gray-800">
              {formatCurrency(rental.baseAmount)}
            </p>
          </div>
          
          <div>
            <p className="text-sm text-gray-600">Monto Actual</p>
            <p className="text-xl font-bold text-green-600">
              {formatCurrency(currentDisplayAmount)}
            </p>
            {calculation && calculation.periods > 0 && (
              <p className="text-xs text-gray-500">
                +{calculation.totalInflation.toFixed(2)}% desde última actualización
              </p>
            )}
          </div>
        </div>

        {/* Update Info */}
        <div className="border-t pt-3">
          <div className="flex justify-between items-center text-sm">
            <span className="text-gray-600">Próxima actualización:</span>
            <span className={`font-medium ${isDue ? 'text-red-600' : 'text-gray-800'}`}>
              {formatDate(nextUpdate)}
            </span>
          </div>
          
          <div className="flex justify-between items-center text-sm mt-1">
            <span className="text-gray-600">Frecuencia:</span>
            <span className="text-gray-800">
              Cada {rental.updateFrequency.value} {rental.updateFrequency.unit === 'months' ? 'mes' : 'día'}{rental.updateFrequency.value !== 1 ? (rental.updateFrequency.unit === 'months' ? 'es' : 's') : ''}
            </span>
          </div>
          
          {rental.lastUpdateDate && (
            <div className="flex justify-between items-center text-sm mt-1">
              <span className="text-gray-600">Última actualización:</span>
              <span className="text-gray-800">
                {formatDate(new Date(rental.lastUpdateDate))}
              </span>
            </div>
          )}
        </div>

        {/* Details preview */}
        {rental.details && (
          <div className="mt-3 pt-3 border-t">
            <p className="text-sm text-gray-600 truncate">
              {rental.details}
            </p>
          </div>
        )}
        
        {calculation?.isRealData === false && (
          <div className="mt-2 text-xs text-yellow-600">
            ⚠️ Usando datos estimados (sin conexión al INDEC)
          </div>
        )}
      </div>
    </Link>
  )
}

export default RentalCard
