'use client'

import React, { useEffect, useState } from 'react'
import { Rental } from '@/types/rental'
import { getRentals } from '@/lib/storage'
import { checkRentalsOnAppOpen } from '@/lib/notifications-final'
import RentalCard from './RentalCard'

const RentalList: React.FC = () => {
  const [rentals, setRentals] = useState<Rental[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const loadRentals = async () => {
      try {
        const storedRentals = getRentals()
        setRentals(storedRentals)
        
        // Verificar notificaciones al cargar
        if (storedRentals.length > 0) {
          await checkAndNotifyDueRentals(storedRentals)
        }
      } catch (error) {
        console.error('Error cargando alquileres:', error)
      } finally {
        setLoading(false)
      }
    }

    loadRentals()
  }, [])

  if (loading) {
    return (
      <div className="text-center py-8">
        <div className="animate-pulse">
          <div className="h-4 bg-gray-300 rounded w-1/4 mx-auto mb-4"></div>
          <div className="space-y-3">
            <div className="h-20 bg-gray-300 rounded"></div>
            <div className="h-20 bg-gray-300 rounded"></div>
          </div>
        </div>
      </div>
    )
  }

  if (rentals.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="mb-6">
          <div className="w-24 h-24 mx-auto mb-4 bg-gray-200 rounded-full flex items-center justify-center">
            <span className="text-4xl text-gray-400">🏠</span>
          </div>
          <h3 className="text-xl font-semibold text-gray-700 mb-2">
            No hay alquileres registrados
          </h3>
          <p className="text-gray-500 mb-6">
            Comienza agregando tu primer alquiler para recibir recordatorios de actualización
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-4">
      <div className="mb-6">
        <h2 className="text-xl font-semibold text-gray-800 mb-2">
          Mis Alquileres ({rentals.length})
        </h2>
        <p className="text-gray-600 text-sm">
          Mantén tus propiedades actualizadas con la inflación
        </p>
      </div>
      
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
        {rentals.map((rental) => (
          <RentalCard key={rental.id} rental={rental} />
        ))}
      </div>
    </div>
  )
}

export default RentalList
