import { Rental, RentalUpdate } from '@/types/rental'

const RENTALS_KEY = 'rentals'
const UPDATES_KEY = 'rental_updates'

export const getRentals = (): Rental[] => {
  try {
    if (typeof window === 'undefined') return []
    const data = localStorage.getItem(RENTALS_KEY)
    return data ? JSON.parse(data) : []
  } catch (error) {
    console.error('Error reading rentals from storage:', error)
    return []
  }
}

export const saveRentals = (rentals: Rental[]): void => {
  try {
    if (typeof window === 'undefined') return
    localStorage.setItem(RENTALS_KEY, JSON.stringify(rentals))
  } catch (error) {
    console.error('Error saving rentals to storage:', error)
  }
}

export const addRental = (rental: Rental): void => {
  const rentals = getRentals()
  const updatedRentals = [...rentals, rental]
  saveRentals(updatedRentals)
}

export const updateRental = (id: string, updatedRental: Partial<Rental>): void => {
  const rentals = getRentals()
  const index = rentals.findIndex(r => r.id === id)
  if (index !== -1) {
    rentals[index] = { ...rentals[index], ...updatedRental, lastUpdated: new Date().toISOString() }
    saveRentals(rentals)
  }
}

export const deleteRental = (id: string): void => {
  const rentals = getRentals()
  const filteredRentals = rentals.filter(r => r.id !== id)
  saveRentals(filteredRentals)
}

export const getRentalById = (id: string): Rental | undefined => {
  const rentals = getRentals()
  return rentals.find(r => r.id === id)
}

// Funciones para historial de actualizaciones
export const getRentalUpdates = (): RentalUpdate[] => {
  try {
    if (typeof window === 'undefined') return []
    const data = localStorage.getItem(UPDATES_KEY)
    return data ? JSON.parse(data) : []
  } catch (error) {
    console.error('Error reading rental updates from storage:', error)
    return []
  }
}

export const saveRentalUpdate = (update: RentalUpdate): void => {
  try {
    if (typeof window === 'undefined') return
    const updates = getRentalUpdates()
    const newUpdates = [...updates, update]
    localStorage.setItem(UPDATES_KEY, JSON.stringify(newUpdates))
  } catch (error) {
    console.error('Error saving rental update to storage:', error)
  }
}

export const getRentalUpdatesByRentalId = (rentalId: string): RentalUpdate[] => {
  const updates = getRentalUpdates()
  return updates.filter(u => u.rentalId === rentalId)
}
