'use client'

import { useEffect, useState } from 'react'
import Link from 'next/link'
import RentalList from '@/components/RentalList'
import { checkRentalsOnAppOpen } from '@/lib/notifications'

export default function Home() {
  const [isClient, setIsClient] = useState(false)

  useEffect(() => {
    setIsClient(true)
    
    // Verificar alquileres vencidos al abrir la app
    checkRentalsOnAppOpen()
  }, [])

  if (!isClient) {
    return (
      <main className="p-6 max-w-4xl mx-auto">
        <div className="text-center">
          <h1 className="text-3xl font-bold mb-6 text-gray-800">Recordatorios de Alquiler</h1>
          <p className="text-gray-600">Cargando...</p>
        </div>
      </main>
    )
  }

  return (
    <main className="p-6 max-w-4xl mx-auto min-h-screen">
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold mb-2 text-gray-800">Recordatorios de Alquiler</h1>
        <p className="text-gray-600">Gestiona tus propiedades y mantente al día con las actualizaciones</p>
      </div>
      
      <RentalList />
      
      {/* Botón flotante para agregar */}
      <div className="fixed bottom-6 right-6 z-50">
        <Link href="/add">
          <button className="w-14 h-14 bg-blue-600 text-white rounded-full shadow-lg hover:bg-blue-700 transition-all duration-200 flex items-center justify-center text-2xl font-bold hover:scale-105">
            +
          </button>
        </Link>
      </div>
    </main>
  )
}
